import React from 'react'
import './home.css'

/* react icons */
import { MdKeyboardArrowDown } from 'react-icons/md';

import myPhoto from '../images/homeImages/my-photo.jpg';
function Home() {
    return (
     
        <div id="home" className="home">
            <div className="container">
                <div className="row min-vh-100 align-items-center align-content-center">

                    <div className="col-md-6 homeRight border">
                        <div className="home-img text-center">
                            <img src={myPhoto} alt="berkay" className="rounded-circle" />
                        </div>
                    </div>

                    <div className="col-md-6 homeLeft border">
                        <div className="home-text">
                            <h2>Hi  </h2>
                            <h1>Berkay Oral</h1>
                            <h3>Freelance <span>Developer</span> </h3>
                            <a  href="#about"> <MdKeyboardArrowDown className="arrowDownIcon"/> </a>
                        </div>
                    </div>

                </div>
            </div>
        </div>         
    )
}

export default Home
